import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const Index = resolve => {
  import ('@/pages').then(module => {
    resolve(module)
  })
}

const Log = resolve => {
  import ('@/pages/account/log').then(module => {
    resolve(module)
  })
}
const Reg = resolve => {
  import ('@/pages/account/reg').then(module => {
    resolve(module)
  })
}

export default new Router({
  routes: [{
      path: '/',
      component: Index
    },
    {
      path: '/log',
      component: Log
    },
    {
      path: '/reg',
      component: Reg
    }
  ]
})



